import React,{Component} from 'react'
import TodoItem from './toDoItem'

class TodoList extends Component{
    state={
        val: '',
        items: [],
        count: 0
    }

    onTextChange=(e)=>{
        this.setState({
            val: e.target.value
        })
       
    }
    handleKeyPress=(event)=>{
        if(event.key==='Enter'){
            this.setState({
                items: [...this.state.items, 
                    {value: this.state.val,
                    isCheckedVal: false,
                    id: this.state.count++
                    }
                ],
                val:''
            })
        }
        
       
    }
    isChecked=(itemId)=>{
        const itemValue = this.state.items.filter((item)=>itemId===item.id)
        const newItems = {...itemValue[0],isCheckedVal:true}
       
        this.setState({
            items: [...this.state.items,{newItems}]
        },()=>console.log(this.state.items))
        
    }

    render(){
        const { val, items, checkStatus } = this.state;
        return(
            <div>
                <input type="text" name="todo" value={this.state.val} onChange={this.onTextChange}
                onKeyPress={this.handleKeyPress}/>
                <ul>
                {items.map((item,i)=>{
                     return(
                    <li> <TodoItem key={i} item={item} checked={this.isChecked} /></li>
                     )
                })}
                </ul>

               
            </div>
        )
    }
}

export default TodoList